<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card>
            <el-row>
                <el-col :span="8">
                     <el-input placeholder="" v-model="orderQuery.query" class="input-with-select" @clear="getOrdersList" >
                        <el-button slot="append" icon="el-icon-search" @click="getOrdersList"></el-button>
                    </el-input>
                </el-col>
            </el-row>
            <!--  -->
            <el-table :data="orderList" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="order_number"></el-table-column>
        <el-table-column label="" prop="order_price"></el-table-column>
        <el-table-column label="" prop="pay_status">
          <template slot-scope="scope">
            <el-tag type="success" v-if="scope.row.pay_status === '1'"></el-tag>
            <el-tag type="danger" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" prop="is_send">
          <template slot-scope="scope">
            <template>
              {{scope.row.is_send}}
            </template>
          </template>
        </el-table-column>
        <el-table-column label="" prop="create_time">
          <template slot-scope="scope">
            {{scope.row.create_time | dateFormat}}
          </template>
        </el-table-column>
        <el-table-column label="">
          <template>
            <el-button size="mini" type="primary" icon="el-icon-edit" @click="showBox"></el-button>
            <el-button size="mini" type="success" icon="el-icon-location" @click="showProgressBox"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="orderQuery.pagenum"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="orderQuery.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
        </el-pagination>
        </el-card>
        <!--  -->
        <el-dialog
            title=""
            :visible.sync="addressVisible"
            width="50%"
            @close="resetform">
            <el-form :model="address" :rules="addressRules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="" prop="address1">
                    <el-cascader
                        v-model="address.address1"
                        :options="cityDate"
                        :props="{ expandTrigger: 'hover' }"
                        ></el-cascader>
                </el-form-item>
                <el-form-item label="" prop="address2">
                    <el-input v-model="address.address2"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="addressVisible = false"> </el-button>
                <el-button type="primary" @click="addressVisible = false"> </el-button>
            </span>
            </el-dialog>

            <!--  -->
            <el-dialog
                title=""
                :visible.sync="processVisible"
                width="50%">
                 <el-timeline :reverse="true">
                    <el-timeline-item
                    v-for="(activity, index) in processList"
                    :key="index"
                    :timestamp="activity.time">
                    {{activity.context}}
                    </el-timeline-item>
                </el-timeline>
            </el-dialog>
    </div>
</template>
<script>
import cityDate from './citydata.js'
export default {
  data() {
    return {
      orderList: [],
      orderQuery: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      total: 0,
      addressVisible: false,
      address: {
        address1: [],
        address2: ''
      },
      addressRules: {
        address1: [
          { required: true, message: '', trigger: 'blur' }
        ],
        address2: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      cityDate,
      processVisible: false,
      processList: []
    }
  },
  created() {
    this.getOrdersList()
  },
  methods: {
    // 
    async getOrdersList() {
      const { data: res } = await this.$http.get('orders', { params: this.orderQuery })
      console.log(res)
      if (res.meta.status !== 200) return this.$message.error('')
      this.orderList = res.data.goods
      this.total = res.data.total
      console.log(1)
      console.log(this.orderQuery)
    },
    showBox() {
      this.addressVisible = true
    },
    async showProgressBox() {
      // 
      const { data: res } = await this.$http.get('/kuaidi/1106975712662')
      console.log(res)
      if (res.meta.status !== 200) return this.$message.error('')
      this.processList = res.data
      this.processVisible = true
    },
    handleSizeChange(newsize) {
      this.orderQuery.pagesize = newsize
      this.getOrdersList()
    },
    handleCurrentChange(newnum) {
      this.orderQuery.pagenum = newnum
      this.getOrdersList()
    },
    resetform() {
      this.$refs.ruleForm.resetFields()
    }

  }
}
</script>
